.kpi-block {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow:
    0 2px 8px rgba(0, 0, 0, 0.08),
    0 8px 24px rgba(0, 0, 0, 0.12);
  transition: all 0.3s ease;
  border: 1px solid #e5e7eb;
  border-left: 4px solid #3b82f6;

  &:hover {
    box-shadow:
      0 4px 16px rgba(0, 0, 0, 0.15),
      0 12px 32px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

.kpi-header {
  margin-bottom: 20px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--surface-100);
}

.kpi-title {
  color: var(--text-color);
  font-size: 1.125rem;
  font-weight: 700;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.metrics-container {
  display: grid;
  gap: 20px;

  &.orientation-horizontal {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }

  &.orientation-vertical {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}

.metric-item {
  background: #f9fafb;
  border-radius: 10px;
  padding: 20px;
  transition: all 0.2s ease;
  border: 1px solid #e5e7eb;
  position: relative;
  overflow: hidden;

  &:hover {
    background: #f3f4f6;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border-color: #d1d5db;
  }
}

.metric-main {
  margin-bottom: 12px;
}

.metric-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-600);
  line-height: 1.2;
  margin-bottom: 6px;
}

.metric-label {
  font-size: 0.8125rem;
  color: var(--text-color-secondary);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.75px;
}

.metric-comparison {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding-top: 12px;
  border-top: 1px solid var(--surface-200);

  &.improvement {
    --comparison-color: #22c55e;
  }

  &.decline {
    --comparison-color: #ef4444;
  }
}

.comparison-indicator {
  display: flex;
  align-items: center;
  gap: 6px;
  color: var(--comparison-color);
  font-weight: 600;

  i {
    font-size: 0.875rem;
    transition: transform 0.2s ease;
  }

  .comparison-percentage {
    font-size: 0.875rem;
  }
}

.comparison-label {
  font-size: 0.75rem;
  color: var(--text-color-secondary);
}

// Trend Icons
.trend-up {
  &::before {
    content: '↗';
  }
  animation: bounce-up 2s infinite;
}

.trend-down {
  &::before {
    content: '↘';
  }
  animation: bounce-down 2s infinite;
}

.trend-stable {
  &::before {
    content: '→';
  }
}

@keyframes bounce-up {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-3px);
  }
  60% {
    transform: translateY(-1px);
  }
}

@keyframes bounce-down {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(3px);
  }
  60% {
    transform: translateY(1px);
  }
}

// Theme Variants
.kpi-block.theme-primary {
  border-left-color: #3b82f6;
}

.kpi-block.theme-success {
  border-left-color: #22c55e;
}

.kpi-block.theme-warning {
  border-left-color: #f59e0b;
}

.kpi-block.theme-danger {
  border-left-color: #ef4444;
}

// Dark Mode Support
:host-context(.dark) {
  .kpi-block {
    background: var(--surface-800);
    border-color: var(--surface-600);
  }

  .metric-item {
    background: var(--surface-700);
    border-color: var(--surface-600);

    &:hover {
      background: var(--surface-600);
    }
  }

  .metric-comparison {
    border-top-color: var(--surface-600);
  }
}

// Responsive Design
@media (max-width: 768px) {
  .kpi-block {
    padding: 16px;
  }

  .metrics-container {
    &.orientation-horizontal {
      grid-template-columns: 1fr;
    }
  }

  .metric-value {
    font-size: 1.875rem;
  }

  .metric-item {
    padding: 16px;
  }
}

@media (max-width: 480px) {
  .metric-value {
    font-size: 1.625rem;
  }

  .metrics-container {
    gap: 12px;
  }
}
